import m from "mithril";
export const Default = {
    view: ({ attrs }) => {
        console.log("attrs.title", attrs.title);
        return m("div", {
            className: attrs.className,
            "data-test-id": `content-default${attrs.contentId ? `-${attrs.contentId}` : ''}`
        }, [
            m("h2", attrs.title),
            m("button", {
                className: "button",
                onclick: () => attrs.hide(),
                "data-test-id": "button-hide-content",
            }, "Hide from component")
        ]);
    }
};
